import React from 'react';
import Layaout from '../components/Layaout/layaout'
import Footer from '../components/Layaout/footer';
import Link from 'next/link'

const Login = () => {
    return (
        <Layaout>
      <div className="global-container">
        <div className="card login-form holaa">
          <div className="card-body">
            <img className="text-center" src="static/imgs/LOGO_GRANDI.png" alt="" />
            <div className="card-text">
              {/*
			<div class="alert alert-danger alert-dismissible fade show" role="alert">Incorrect username or password.</div> */}
              <form>
                {/* to error: add class "has-danger" */}
                <div className="form-group">
                  <input type="email" className="form-control form-control-sm" placeholder="Usuario"  id="exampleInputEmail1" aria-describedby="emailHelp" />
                </div>
                <div className="form-group">
                  <input type="password" className="form-control form-control-sm " placeholder="Contraseña" id="exampleInputPassword1" />
                </div>
                <div className="checkbox">
                  <label><input type="checkbox" /> Recuerdame </label>
                </div>
                <button type="submit" className="btn btn-primary btn-block">Ingresa</button>
                <div className="sign-up">
                  ¿No tienes una cuenta? <a href="#">Regístrate</a>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
      </Layaout>
    )
}

export default Login